            <th colspan="2">Shundan</th>
            <th colspan="9">Savollarga javoblar*</th>
          </tr>
          <tr>
            <th colspan="2"></th>
            <th>Oʻgʻil</th>
            <th>Qiz</th>
            <th colspan="2"></th>
            <th>Oʻgʻil</th>
