          <tbody>
          <!-- Example row -->
          <tr>
            <td>Respublika bo`yicha jami:</td>
            <td></td>

            <td colspan="2"></td>
            <td> </td>
            <td> </td>

            <td colspan="2">{{all_sum}}</td>
            <td>{{gender_all_m}}</td>
            <td>{{gender_all_w}}</td>
            <td>{{all_sum}}</td>
            <td>{{all_sum}}</td>
            <td>{{all_sum}}</td>
            <td>{{all_sum}}</td>
            <td></td>
            <td></td>
