          </tr>
          <tr v-for="(item,index) in regions_main" :key="index">
            <td>
              {{index+1}}
            </td>
            <td colspan="2">
              <router-link :to="`/eleven_organ_child/${item.organizationid}`">
                {{item.organizationfullname}}
              </router-link>
            </td>
            <td>

            </td>
            <td></td>
            <td ></td>
            <td colspan="2">{{statistiks[index].total_students}}</td>
            <td>{{gender_m[index]}}</td>
