    </div>
  </div>
</template>
<script>
import axios from "axios";
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
export default {
  data(){
    return{
      all_users:[],
      statistiks:[],
      statsExel: [],
      regions_main:[],
      sum:[],
      all_sum:0,
      gender_m:[],
      gender_w:[],
      gender_all_m:0,
      gender_all_w:0,
    }
  },
  mounted() {
    axios.get(`https://api-kasb.maktab.uz/api/v1/integration/organizations/?district_id=${this.$route.params.id}`)
      .then(res=>{
        this.regions_main=res.data.result
        this.regions_main.forEach(el=>{
          this.sum.push(el.organizationid)
        })
        console.log(this.sum)
        axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers1/?school_id=${this.sum}&&class_id=11`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language': 'uz'
          }
        })
          .then(res=>{
            this.statistiks=res.data.statistics
            this.statistiks.forEach(el=>{
              this.all_sum=this.all_sum+el.total_students
            })
          })

        axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers1/?school_id=${this.sum}&&class_id=11&&gender_id=1`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language': 'uz'
          }
        })
          .then(res=>{
            res.data.statistics.forEach(el=>{
              console.log(el.total_students)
              this.gender_m.push(el.total_students)
              this.gender_all_m=this.gender_all_m+el.total_students

            })
            console.log(this.gender_m,"dsalkm")
          })
        axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers1/?school_id=${this.sum}&&class_id=11&&gender_id=2`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language': 'uz'
          }
        })
          .then(res=>{
            res.data.statistics.forEach(el=>{
              console.log(el.total_students)
              this.gender_w.push(el.total_students)
              this.gender_all_w=this.gender_all_w+el.total_students
            })
            console.log(this.gender_m,"dsalkm")
          })

      })


  },
  methods:{
    exportTableToExcel() {
      const table = document.getElementById('my-table');
      const worksheet = XLSX.utils.table_to_sheet(table);
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');

      const wbout = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });

      try {
        saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'table_data.xlsx');
      } catch (e) {
        if (typeof console !== 'undefined') console.log(e, wbout);
      }
    }
  }
}
</script>
